<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <h2>{{id?'编辑':'新增'}}留言</h2>
    </div>
    <el-form :label-position="labelPosition" label-width="100px" @submit.native.prevent="saveMessage">
      <el-form-item label="昵称：" style="margin-left: 20%;width: 500px">
        <el-input v-model="model.nickname"></el-input>
      </el-form-item>
      <el-form-item label="留言内容：" style="margin-left: 20%;width: 500px;">
        <el-input
          type="textarea"
          :autosize="{ minRows: 3, maxRows: 5}"
          placeholder="请输入内容"
          v-model="model.content">
        </el-input>
      </el-form-item>
      <el-form-item label="头像" style="width: 50%;margin-left: 20%">
        <el-upload
          class="avatar-uploader"
          :action="$http.defaults.baseURL + '/uploads'"
          :show-file-list="false"
          :headers="getAuthHeaders()"
          :on-success="handleImageSuccess">
          <img v-if="model.avatar" :src="model.avatar" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item style="margin-left: 35%">
        <el-button type="success" round  native-type="submit">提交</el-button>
      </el-form-item>

    </el-form>
  </el-card>
</template>

<script>
  import auth from '../../mixin/index'
  export default {
    name: "AdminUserEdit",
    props:['id'],
    mixins:[auth],
    data(){
      return {
        labelPosition: 'right',
        model:{
          user:'',
          nickname:'',
          avatar:''
        }
      }
    },
    methods:{
      handleImageSuccess(res){
        this.model.avatar = res.url
      },
      async saveMessage(){
        if(this.id){
          await this.$http.put(`rest/message/${this.id}`,this.model)
          this.$message.success('修改留言成功')
          this.$router.push('/message/list')

        }else {
          await this.$http.post(`/rest/message`,this.model)
          this.$message.success('添加留言成功')
          this.$router.push('/message/list')
        }
      },
      async getOneMessageData(){
        const res = await this.$http.get(`/rest/message/${this.id}`)
        this.model = res.data
      }

    },
    created() {
      this.id && this.getOneMessageData()
    }
  }
</script>

<style lang="scss" scoped>

  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
    border: 1px dashed #d9d9d9;
    &:hover{
      border-color: #409EFF;
    }
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
